.grid {
    position: relative;
    display: grid;
    grid-template-columns: 2fr 2fr 2fr;
    grid-column-gap: 2vw;
    padding-top: 30px;
    padding-bottom: 30px;
}
.container-fluid {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

#projects .card {
    position: relative;
    top: 0;
    height: 55vh;
    margin-bottom: 4vh;
    text-align: center;
    background: white;
    overflow: hidden;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
    border-radius: 20px;
}
.card {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
    border-radius: 85rem;
    box-shadow: 0px 0px 20px #000000;
}

#projectText {
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-column-gap: 20px;
    margin: auto;
    text-align: center;
}

#projectName {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 0.8em;
    margin: auto;
    padding: 2px;
}

#projectBrief {
    display: flex;
    text-align: left;
    align-items: center;
    justify-content: center;
    font-size: 0.8em;
    color: grey;
}

#project-picture{
    display: inline-block;
    float: left;
    text-align: left;
    border: 1px solid black;
    width: 400px;
    height: 500px;
    -webkit-transform: translate(20px, 30px);
            transform: translate(20px, 30px);
    border-radius: 85px;
}

#project-content{
    display: inline-block;
    position: relative;
    width: 50%;
}

#parent-modal{
    display: inline-block;
